<template>
  <div class="search-form">
    <h2>查询课程评价</h2>
    <form @submit.prevent="searchCourses">
      <div class="form-group">
        <label for="course_name">课程名称:</label>
        <input type="text" v-model="course_name" id="course_name" class="form-control">
      </div>
      <div class="form-group">
        <label for="instructor">授课老师:</label>
        <input type="text" v-model="instructor" id="instructor" class="form-control">
      </div>
      <button type="submit" class="btn btn-primary">查询</button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      course_name: '',
      instructor: ''
    };
  },
  methods: {
    searchCourses() {
      this.$emit('search', { course_name: this.course_name, instructor: this.instructor });
    }
  }
};
</script>

<style>
.search-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}
.form-group {
  margin-bottom: 15px;
}
.form-control {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.btn {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.btn:hover {
  background-color: #0056b3;
}
</style>
