<template>
  <div id="app">
    <header>
      <h1>武汉大学课程评价查询系统</h1>
    </header>
    <main>
      <button @click="currentPage = 'add'" v-if="!courseEvaluationFilled">添加课程评价</button>
      <button @click="currentPage = 'search'" v-if="courseEvaluationFilled">查询课程评价</button>
      <AddCourse v-if="currentPage === 'add'" @courseAdded="courseAdded"></AddCourse>
      <div v-if="currentPage === 'search'">
        <SearchForm @search="handleSearch"></SearchForm>
        <div v-if="results.length">
          <p>共查询到 {{ results.length }} 条结果</p>
          <SearchResults :results="results"></SearchResults>
        </div>
        <div v-else>
          <p>未找到查询结果</p>
        </div>
      </div>
    </main>
    <footer>
      <p>&copy; 2024 JeredGong. All rights reserved.</p>
    </footer>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.baseURL='/api'
import Cookies from 'js-cookie';
import AddCourse from './components/AddCourse.vue';
import SearchForm from './components/SearchForm.vue';
import SearchResults from './components/SearchResults.vue';

export default {
  components: {
    AddCourse,
    SearchForm,
    SearchResults
  },
  data() {
    return {
      results: [],
      currentPage: 'add',
      courseEvaluationFilled: false
    };
  },
  created() {
    this.courseEvaluationFilled = Cookies.get('courseEvaluationFilled') === 'true';
    if (this.courseEvaluationFilled) {
      this.currentPage = 'search';
    }
  },
  methods: {
    courseAdded() {
      this.courseEvaluationFilled = true;
      Cookies.set('courseEvaluationFilled', 'true', { expires: 7 });
      this.currentPage = 'search';
    },
    async handleSearch(searchParams) {
      try {
        console.log('Sending search request with params:', searchParams);
        const response = await axios.get('http://103.20.220.93:5000/search', {
          params: searchParams
        });
        console.log('Received response:', response.data);

        let responseData = response.data;

        if (typeof responseData === 'string') {
          try {
            responseData = JSON.parse(responseData);
          } catch (e) {
            console.error('Error parsing response data as JSON:', e);
            return;
          }
        }

        if (Array.isArray(responseData)) {
          const results = responseData.map(item => {
            return {
              ...item,
              '课程成绩': item['课程成绩'] === null || isNaN(item['课程成绩']) ? '' : item['课程成绩']
            };
          });
          console.log('Processed results:', results);
          this.results = results;
        } else {
          console.error('Unexpected response format:', typeof responseData, responseData);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }
  }
};
</script>

