<template>
  <div class="add-course">
    <h2>添加课程评价</h2>
    <h3>您必须先完成课程评价后，才能查询课程评价。为了维护良好生态，请您务必认真填写评价！</h3>
    <h4>删除Cookies后可能需要重新填写课程评价</h4>
    <form @submit.prevent="addCourse">
      <div class="form-group">
        <label for="course_name">课程名称:</label>
        <input type="text" v-model="course_name" id="course_name" class="form-control" required>
      </div>
      <div class="form-group">
        <label for="instructor">授课老师:</label>
        <input type="text" v-model="instructor" id="instructor" class="form-control" required>
      </div>
      <div class="form-group">
        <label for="content">课程内容:</label>
        <input type="text" v-model="content" id="content" class="form-control" required>
      </div>
      <div class="form-group">
        <label for="attendance">考勤与平时作业:</label>
        <input type="text" v-model="attendance" id="attendance" class="form-control" required>
      </div>
      <div class="form-group">
        <label for="assessment">考核方式:</label>
        <input type="text" v-model="assessment" id="assessment" class="form-control" required>
      </div>
      <div class="form-group">
        <label for="grade">课程成绩:</label>
        <input type="number" v-model="grade" id="grade" class="form-control" required>
      </div>
      <button type="submit" class="btn btn-primary">提交评价</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie';

export default {
  data() {
    return {
      course_name: '',
      instructor: '',
      content: '',
      attendance: '',
      assessment: '',
      grade: ''
    };
  },
  methods: {
    async addCourse() {
      const courseData = {
        course_name: this.course_name,
        instructor: this.instructor,
        content: this.content,
        attendance: this.attendance,
        assessment: this.assessment,
        grade: this.grade
      };

      // 检查课程成绩是否在0-100范围内
      if (!Number.isInteger(courseData.grade) || courseData.grade < 0 || courseData.grade > 100) {
        alert('课程成绩必须是0-100范围内的整数');
        return;
      }

      try {
        await axios.post('http://127.0.0.1:5000/add_course', courseData);
        alert('课程评价提交成功');
        Cookies.set('courseEvaluationFilled', 'true', { expires: 7 });
        this.$emit('courseAdded');
      } catch (error) {
        console.error('Error adding course:', error);
        alert('课程评价提交失败');
      }
    }
  }
};
</script>

<style>
.add-course {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}
.form-group {
  margin-bottom: 15px;
}
.form-control {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.btn {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.btn:hover {
  background-color: #0056b3;
}
</style>
